import { all } from 'redux-saga/effects';
import {
  getForm,
  saveFormData,
  saveFormFiles,
} from 'components/FormBuilder/redux/Sagas';
import { getForms } from 'components/SellerProperty/Listing/Form/redux/Sagas';

export default function* rootSaga() {
  yield all([getForm(), saveFormData(), saveFormFiles(), getForms()]);
}
