import produce from 'immer';
import uuid from 'uuid';
import { FILE_UPLOADER_ACTION_ADD } from '../shared/Consts';
import {
  SURVEYJS,
  ELEMENTS_TYPE,
  SURVEYJS_MODE,
  SURVEYJS_STATUS,
} from '../components/FormBuilder/Constants';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import _has from 'lodash/has';

export const isFormInDisplayMode = mode => mode === SURVEYJS_MODE.display;

export const getFormMode = status =>
  status === SURVEYJS_STATUS.submitted
    ? SURVEYJS_MODE.display
    : SURVEYJS_MODE.edit;

export const isFormSubmitted = formData => {
  if (!_has(formData, 'DataStatus')) return false;
  const status = _get(formData, 'DataStatus');
  return status === SURVEYJS_STATUS.submitted;
};

export const switchNameToId = survey => {
  if (!_has(survey, ['questionHashes', 'names'])) return {};
  const questionsName = Object.keys(
    _get(survey, ['questionHashes', 'names'], {}),
  );
  return questionsName.reduce((accumulator, name) => {
    const questionId = _get(survey.getQuestionByName(name), 'questionId', '');
    const questionValue = survey.getValue(name);
    return {
      ...accumulator,
      [questionId]: questionValue ?? '',
    };
  }, {});
};

export const applyFormProperty = (question, formProperty) => {
  if (_isEmpty(formProperty)) return;
  const questionId = _get(question, 'questionId');
  if (!formProperty[questionId]) return;
  const properties = Object.entries(formProperty[questionId]);
  properties.forEach(property => {
    const [propertyName, propertyValue] = property;
    if (question[propertyName])
      try {
        question[propertyName] = propertyValue;
      } catch (e) {
        //pass this propery
        //avoid improper properties
      }
  });
};

const fileQuestionDataParser = (questionsData, fileQuestions) => {
  if (_isEmpty(questionsData)) return {};
  if (_isEmpty(fileQuestions)) return questionsData;
  return produce(questionsData, draft => {
    const arr = Object.entries(draft);
    return arr.reduce((acc, curr) => {
      const [key, value = ''] = curr;
      if (fileQuestions[key] === ELEMENTS_TYPE.file)
        return { ...acc, [key]: fileDataParser(questionsData[key]) };
      return { ...acc, [key]: value };
    }, {});
  });
};

export const switchIdToName = (survey, data, fileQuestions) => {
  if (!_has(survey, ['questionHashes', 'names'])) return {};
  const questionsName = Object.keys(
    _get(survey, ['questionHashes', 'names'], {}),
  );
  const questionsData = questionsName.reduce((accumulator, name) => {
    const questionId = _get(survey.getQuestionByName(name), 'questionId', '');
    return {
      ...accumulator,
      [name]: data[questionId],
    };
  }, {});
  if (_isEmpty(fileQuestions)) return questionsData;
  /*
  the fileQuestions array contains questions that are file-typed
  the fileQuestions need base64 string to be the data while the data given by the API cannot match the format
  all file-typed questions data will be modified for the surveyjs to accept the data
  */
  return fileQuestionDataParser(questionsData, fileQuestions);
};

const fileDataParser = data => {
  if (_isEmpty(data)) return [];
  return data.reduce((acc, curr) => {
    const file = {
      name: _get(curr, 'name'),
      content: SURVEYJS.fileContent,
      file: _get(curr, 'file'),
      id: _get(curr, 'id'),
      key: _get(curr, 'key'),
      size: _get(curr, 'size'),
      type: _get(curr, 'type'),
    };
    return [...acc, file];
  }, []);
};

export const getFilesToUpload = files => {
  return produce(files, draft => {
    return draft.reduce((acc, curr) => {
      const id = uuid();
      const size = _get(curr, 'size');
      const name = _get(curr, 'name');
      curr.path = name;
      const newFile = {
        id,
        action: FILE_UPLOADER_ACTION_ADD,
        name,
        type: _get(curr, 'type'),
        size,
        file: curr,
        content: { id, size },
      };
      return [...acc, newFile];
    }, []);
  });
};

export const generateFileUpload = file => {
  const content = _get(file, 'content', {});
  const id = _get(content, 'id', {});
  const size = _get(content, 'size', {});
  if (!content || !id || !size) return {};
  return {
    id,
    action: FILE_UPLOADER_ACTION_ADD,
    name: _get(file, 'name'),
    file: { path: _get(file, 'name') },
    type: _get(file, 'type'),
    size,
  };
};

export const resetQuestionValue = options => (options.question.value = []);
